* {
  font-family: "Mukta", sans-serif;
  box-sizing: border-box;
}

.div_left {
  position: relative;
  margin-right: 0.1%;
  margin-left: 0;
  background-image: linear-gradient(#dfdfdf, #f7f7f7, #dfdfdf);
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.div_right {
  position: relative;
  margin-right: 0;
  display: inline-block;
  background-image: linear-gradient(to right, #feb360, #ff916f);
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

img {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  margin-top: 5%;
  margin-bottom: 5%;
  margin-right: 5%;
  float: right;
}

