/* skills sliders */

.container_quality_tools {
  width: 100%;
  margin-top: 20%;
}

.slider_text {
  font-size: 10px;
  margin-bottom: 1%;
  margin-left: 20px;
}
#slider_qt {
  -webkit-appearance: none;
  width: 100%;
  margin-left: 3%;
  margin-right: 0%;
  height: 7px;
  outline: none;
  border-radius: 3px;
}

#slider_qt:focus {
  outline: none;
}

#slider_qt::-webkit-slider-thumb {
  -webkit-appearance: none;
  width: 20px;
  height: 20px;
  cursor: pointer;
  position: relative;
}

#slider_qt::-moz-range-thumb {
  -webkit-appearance: none;
  background: transparent;
  border-color: transparent;
  color: transparent;
  width: 20px;
  height: 20px;
  cursor: pointer;
}

#selector {
 position: absolute;
  margin-left: 90%;
  padding-bottom: 30%;
 
}

.SelectorBtn {
  height: 14px;
  width: 14px;
  background-image: url("../media/slider-icon-14.jpg");
  background-size: cover;
  background-position: center;
  border-radius: 50%;
  bottom: 0;
  z-index: 1;
}

#SelectValue {
  width: 20px;
  height: 20px;
  position: absolute;
  top: -25px;
  left: -2px;
  background: #feb360;
  border-radius: 4px;
  text-align: center;
  line-height: 20px;
  font-size: 8px;
  font-weight: bold;
}

#SelectValue::after {
  content: "";
  border-top: 12px solid #feb360;
  border-left: 10px solid #f1f0f0;
  border-right: 10px solid #f1f1f1;
  position: absolute;
  bottom: -4px;
  left: 0;
  z-index: -1;
}

